import React from "react";
import db from "./Firebase";
import { FcFullTrash } from "react-icons/fc";
import { MdDelete } from "react-icons/md";

function Todo(props) {
     return (
          <div className=" items-center justify-center flex flex-col pr-56 w-full">
               <div className="flex flex-row w-full">
                    <p className="border-red-800 border-b-2 text-3xl">{props.propsName.firebaseArray}</p>
                    <button className="text-3xl text-yellow-400 ml-5" onClick={(event) => db.collection("firebaseDatas").doc(props.propsName.id).delete()}>
                         <MdDelete />
                    </button>
               </div>
               {/* <p className="text-base text-gray-400 text-left w-full">Deadline ‚è∞</p> */}
          </div>
     );
}

export default Todo;
